.movie_content {
  border-radius: 5px;
  padding: 25px;
  box-shadow: 0 13px 27px -5px rgb(255, 246, 193),
    0 8px 16px -8px rgb(255, 73, 1), 0 -6px 16px -6px rgb(255, 187, 147);
}
.movie_content img {
  width: 100%;
  position: relative;
  top: -50px;
  max-width: 250px;
  box-shadow: 0 30px 60px -12px rgb(50 50 93 / 25%),
    0 18px 36px -18px rgb(0 0 0 / 30%), 0 -12px 36px -8px rgb(0 0 0 / 3%);
}
